body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


/* start Header */
.docplannernavbar {
  display : flex;
  justify-content : space-around;
  position :-webkit-sticky ;
  position :sticky ;
  top : 0;
  background-color : white ;
  align-items : center;
   }

   .navbar-light .navbar-nav .nav-link:hover {
    color : #00b39b;
    
    }
    /* end Header */

    /* debut section description */
.description{
  display :flex;
  flex-direction: column;
  justify-content: space-around;
  height:350px;
}
.title {
  display :flex;
  flex-direction : column;
  align-items : center;
  justify-content: space-around;
  height:150px;
    
  }
  .title h1 {
    color : #00b39b;
    font-size : 35px ;  
   
  }
  .description-text {
   display :flex;
   justify-content: center;
  }
  .part-a , .part-b {
   width : 450px;
  color : #747474;
   font-size : 18px
  }
  .part-a  {
  margin-right : 20px;
  }
  .part-b  {
  margin-left : 20px;
  }
  /* fin section description */
  /* debut section patients-doctors */
.patients {
  border-radius: 25px;
    border :  #00b39b ;
   background-color : #00b39b;
    width :450px;
    height: 400px;
   margin-right: 15px;
   overflow: hidden;
   padding : 30px
 }
 .patients h2 {
  color : hsla(0,0%,100%,.7);
  font-size : 15px;
  font-family: Helvetica Neue,Helvetica,Arial,sans-serif;
 
 }
 .patients p {
  color : white;
  font-size : 25px;
  font-family: Helvetica Neue,Helvetica,Arial,sans-serif;
  padding: 5px;
 }
 .choose-country{
  border : 1px solid white ; 
   border-radius: 8px;
   width : 180px;
   height : 40px ;
   text-align: center;
    margin-left : 15px;
    font-size :12px;
    background-color: white;
  
   }
 .choose-country:hover{
  background-color : #d7f5f6;
 }
 .doctors {
   display : flex ;
   flex-direction : column;
  border-radius: 25px;
   border :   #3d83df ;
   background-color : #3d83df;
   width :450px;
   height: 400px;
   overflow :hidden ;
   margin-left: 15px;
   padding : 30px
   }
 .doctors h2 {
  color : hsla(0,0%,100%,.7);
  font-size : 15px;
  font-family: Helvetica Neue,Helvetica,Arial,sans-serif;
   margin-bottom : 0px;
   }
 .doctors p {
  color : white;
  font-size : 25px;
  font-family: Helvetica Neue,Helvetica,Arial,sans-serif;
  padding: 5px;
 }
 .patients-doctors {
    display : flex ;
   justify-content : center;
   margin-top :50px;
 
 }
 .patients img {
  height :250px;
  width : 398px ;
 margin-right: 50px;
 }
 
 .doctors img {
  height :250px;
  width : 398px ;
 margin-left: 150px;
 }
 .doctors-image img {
 height :250px;
 width : 398px ;
 
 }
 .image-choose {
 display : flex;
 width : 600px ;  
 }
 /* fin section patients-doctors */
 /* debut section global-company */
.global-company {
  display : flex ;
  justify-content : space-around;
  margin-top:50px;
 }
 
 .global-company h2 {
  color: #00b39b;
  font-size: 1.625em;
 }
 .svg-campany {
   display :flex ;
   flex-flow: row wrap;
   width : 750px ;
  margin-right: 100px;

 }
 .svg-campany li {
  margin-right : 50px;
  list-style-type: none;
 }
 .global-company h2 {
   margin-left: 170px;
 }
 a svg {
  fill: #747474;
  }
 a svg:hover {
 fill: #00b39b;
 } 
 /* fin section global-company */
 /* debut section platform */
.platform{
  background-color : #d7f5f6; 
   display : flex ;
   justify-content : space-around ;
   overflow :hidden;
   height: 600px;
   width : 100% ;
 }
 
 .leader , .calendar , .visit , .solutions{
   border : solid white ;
   border-radius : 5px;
   background-color : white;
   margin-top : 10px;
   width :230px;
   height: 250px;
   padding : 10px ;
   margin-bottom : 25px ;
   display : flex ;
   flex-direction : column;
   flex-wrap : wrap;
   justify-content : center ;
    align-items : center ;
   text-align : center;
 }
 .stat h4 {
  font-size: 25px;
  color: #00b39b;
  font-family: Helvetica Neue,Helvetica,Arial,sans-serif;
 }
 .stat p {
 font-size: 13px;
  font-family: Helvetica Neue,Helvetica,Arial,sans-serif;
  color: #747474;
   text-align : center ;
 }
 .platform-descp h3 {
   color : #00b39b;
   font-family: Helvetica Neue,Helvetica,Arial,sans-serif;
   font-size : 25px;
 }
 .platform-descp p {
 font-family: Helvetica Neue,Helvetica,Arial,sans-serif;
 color: #747474;
 font-size : 15px;
 } 
 .descp-head  {
  width : 430px;
  margin-left : 150px;
   margin-top : 100px ;
 }
 .big-logo-docplanner{
   margin-top : 50px;
 }
 .stat{
 display : flex; 
 flex-wrap : wrap;
 justify-content : space-around;
 margin-right : 150px ;
 margin-top : 0px;
 }
 .stat-right{
  margin-top : 30px ;
   margin-right : 20px ;
 }
 .stat-left{
  margin-top : 10px ;
  
 }
 
 /* fin section platform */
 /* debut section vision */

 .vision {
   margin-top: 80px;
 }
.vision-description {
  text-align : center;
  display : flex ;
  margin : auto ;
  flex-direction : column;
  width : 500px ;
 }
 .vision-description h4 {
   color: #00b39b;
   font-size: 30px;
   font-family: Helvetica Neue,Helvetica,Arial,sans-serif;
   margin-bottom : 15px ;
   }
   .vision-description p {
  color: #747474;
  font-size: 16px;
  font-family: Helvetica Neue,Helvetica,Arial,sans-serif;
    margin-bottom : 70px ; 
 }
 .citiesnames {
   border : 2.5px solid #f7f7f7;
   border-radius : 10px;
   overflow : hidden;
     margin-bottom : 30px;
 }
 .footer {
  display : flex ;
  justify-content : space-between ;
   align-items : center ;
   padding : 20px;
 
 }
 .cities{ 
   display : flex ;
 }
 .cities input {
   cursor: pointer;
   color : white;
   background-color: #3d83df;
   border : 2px solid #3d83df;
   border-radius :10px;
   height: 30px;
   font-family: Helvetica Neue,Helvetica,Arial,sans-serif;
   font-size:12px;
   text-align : center ;
 }
 .cities p {
 color : #747474 ;
 font-size: 16px;
 font-family: Helvetica Neue,Helvetica,Arial,sans-serif;
 }
 .cities a {
  text-decoration: none; 
 }
 .cities{
  display : flex;
  flex-wrap :wrap ;
  justify-content: space-around;
  margin-left : 180px;
  margin-right : 180px;
  margin-bottom : 60px;
 }
 .citiesnames:hover{ 
   box-shadow: 10px 10px 10px 10px #edf5f5;
 }
 /* fin section vision */
 /* debut section footer */
footer {
  text-align : center ; 
  font-size: 12px;
  color: #ababab;
  text-decoration: none;
   border-top : 2px solid #f7f7f7;
  }
  footer a {
  text-decoration: none;
   color: #3d83df;
    font-family: Helvetica Neue,Helvetica,Arial,sans-serif;
    font-size : 12px; 
  }
  footer a:hover {
  text-decoration: underline;   
  }
  /* fin section footer */
 
